import "pages/css/index.css"
import { adjustTimezone } from "utils/dateFunctions"
import { DisplayMetaTags } from "utils/metaFunctions"
import { sanitizeText } from "utils/textFunctions"
import { changePic, fetchHistory, fetchTagInfo, updateDescription, updateTag } from "./actions/tag"
import Moment from "react-moment"
import { connect, Provider } from "react-redux"
import { Link } from "react-router-dom"
import {
	Button,
	Container,
	Form,
	Header,
	Icon,
	Image,
	List,
	Menu,
	Segment,
	TextArea
} from "semantic-ui-react"
import Marked from "marked"
import LazyLoad from "components/lazyLoad/v1/"
import PageFooter from "components/footer/v1/"
import PageHeader from "components/header/v1/"
import PropTypes from "prop-types"
import React, { Component } from "react"
import store from "store"
import TitleHeader from "components/titleHeader/v1/"
import TrumpImg from "images/trump-white.png"

class Tags extends Component {
	constructor(props) {
		super(props)
		const id = parseInt(this.props.match.params.id, 10)
		const currentState = store.getState()
		const authenticated = currentState.user.authenticated
		const bearer = currentState.user.bearer
		const userId = parseInt(currentState.user.data.id, 10)
		this.state = {
			activeItem: "article",
			authenticated,
			bearer,
			description: "",
			editing: false,
			id,
			files: [],
			userId
		}

		Marked.setOptions({
			renderer: new Marked.Renderer(),
			highlight: function(code) {
				// return require('highlight.js').highlightAuto(code).value;
			},
			pedantic: false,
			breaks: false,
			sanitize: false,
			smartLists: true,
			smartypants: false,
			xhtml: false
		})

		this.props.fetchTagInfo({ id })
		this.onChangeDescription = this.onChangeDescription.bind(this)
		this.onClickEdit = this.onClickEdit.bind(this)
		this.setVersion = this.setVersion.bind(this)
		this.updateTag = this.updateTag.bind(this)
	}

	handleItemClick = (e, { name }) => {
		this.setState({ activeItem: name })
		if (name === "history") {
			this.props.fetchHistory({ id: this.props.id })
		}
	}
	onChangeDescription = (e, { value }) => this.setState({ description: value })
	onClickEdit = () => {
		this.setState({
			description: this.props.description,
			editing: this.state.editing === false
		})
	}
	setVersion = edit => {
		this.setState({
			activeItem: "article",
			editing: false,
			version: edit.version
		})
		this.props.updateDescription({ description: edit.description })
	}
	updateTag = () => {
		this.setState({ editing: false })
		this.props.updateTag({
			bearer: this.state.bearer,
			id: this.props.id,
			description: this.state.description
		})
	}

	render() {
		const { activeItem, authenticated, bearer, description, editing, id, version } = this.state
		const ArticleSection = props => {
			if (editing) {
				return (
					<Form onSubmit={this.updateTag}>
						<Form.Field>
							<TextArea
								onChange={this.onChangeDescription}
								rows={25}
								value={description}
							/>
						</Form.Field>
						<Button color="blue" compact content="Update" fluid type="submit" />
					</Form>
				)
			}
			return (
				<div
					dangerouslySetInnerHTML={{
						__html: sanitizeText(Marked(props.description))
					}}
				/>
			)
		}
		const EditButton = ({ props }) => {
			if (!props.loading) {
				if (authenticated) {
					return (
						<div className="editButtonWrapper">
							<Icon
								className={`editButton ${editing ? "editing" : ""}`}
								name={editing ? "close" : "pencil"}
								onClick={this.onClickEdit}
							/>
						</div>
					)
				}
			}
			return null
		}
		const HistorySection = props => {
			return props.editHistory.map((edit, i) => {
				return (
					<List.Item
						className={`${version === edit.version ? "selected" : null}`}
						key={`editHistory${i}`}
						onClick={() => this.setVersion(edit)}
					>
						<Image size="mini" src={edit.user_img} />
						<List.Content>
							<List.Header as="a">{edit.user_name}</List.Header>
							<List.Description>
								Edited <Moment date={adjustTimezone(edit.date_updated)} fromNow />
							</List.Description>
						</List.Content>
					</List.Item>
				)
			})
		}
		const TagMenu = props => (
			<Menu className="tagMenu" fluid stackable tabular>
				<Menu.Item
					name="article"
					active={activeItem === "article"}
					onClick={this.handleItemClick}
				/>
				<Menu.Item
					name="history"
					active={activeItem === "history"}
					onClick={this.handleItemClick}
				/>
			</Menu>
		)
		const TagTitle = ({ props }) => {
			const subheader = (
				<div className="subHeader">
					{props.createdBy && (
						<div>
							<Icon className="tag" name="tag" /> Created{" "}
							<Moment
								date={adjustTimezone(props.dateCreated)}
								fromNow
								interval={60000}
							/>{" "}
							by{" "}
							<Link to={`/users/${props.createdBy.username}`}>
								{props.createdBy.name}
							</Link>
						</div>
					)}
				</div>
			)
			return (
				<TitleHeader
					bearer={bearer}
					canEdit={false}
					id={id}
					subheader={subheader}
					title={props.name}
					type="tag"
				/>
			)
		}

		return (
			<Provider store={store}>
				<div className="tagsPage">
					<DisplayMetaTags page="tags" props={this.props} state={this.state} />
					<PageHeader {...this.props} />
					{!this.props.error ? (
						<Container className="mainContainer" textAlign="left">
							<TagTitle props={this.props} />
							{this.props.loading ? (
								<div>
									<LazyLoad header={false} />
									<LazyLoad header={false} />
								</div>
							) : (
								<div className="tagsWrapper">
									{TagMenu(this.props)}
									<Segment stacked>
										{activeItem === "article" && (
											<div>
												<EditButton props={this.props} />
												{ArticleSection(this.props)}
											</div>
										)}
										{activeItem === "history" && (
											<List divided>{HistorySection(this.props)}</List>
										)}
									</Segment>
								</div>
							)}
						</Container>
					) : (
						<Container className="mainContainer" text textAlign="center">
							<Image centered className="trumpImg404" size="medium" src={TrumpImg} />
							<Header size="medium">This tag does not exist!</Header>
						</Container>
					)}
					<PageFooter />
				</div>
			</Provider>
		)
	}
}

Tags.propTypes = {
	changePic: PropTypes.func,
	createdBy: PropTypes.shape({
		id: PropTypes.number,
		img: PropTypes.string,
		name: PropTypes.string,
		username: PropTypes.string
	}),
	dateCreated: PropTypes.string,
	description: PropTypes.string,
	editHistory: PropTypes.array,
	error: PropTypes.bool,
	fetchHistory: PropTypes.func,
	fetchTagInfo: PropTypes.func,
	id: PropTypes.number,
	img: PropTypes.string,
	loading: PropTypes.bool,
	name: PropTypes.string,
	updateDescription: PropTypes.func,
	updateTag: PropTypes.func
}

Tags.defaultProps = {
	changePic,
	editHistory: [],
	fetchHistory,
	fetchTagInfo,
	loading: true,
	updateDescription,
	updateTag
}

const mapStateToProps = (state, ownProps) => {
	return {
		...state.tag,
		...ownProps
	}
}

export default connect(
	mapStateToProps,
	{
		changePic,
		fetchHistory,
		fetchTagInfo,
		updateDescription,
		updateTag
	}
)(Tags)
